import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApicallService } from 'src/app/services/apicall.service';
import { GlobalService } from 'src/app/services/global.service';
import { ToastService } from 'src/app/services/toast.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.page.html',
  styleUrls: ['./register.page.scss'],
})
export class RegisterPage implements OnInit {
  public usersignupdata:any= { username:'', password:'' , role:'admin', status:'admin'}
  constructor(private router:Router, public apicall : ApicallService, public global : GlobalService, public toast : ToastService ) { }

  ngOnInit() {
  }

  signup(){
    console.log(this.usersignupdata);
    this.apicall.api_signup(this.usersignupdata);
    this.usersignupdata = { username:'', password:'', role:'admin', status:'admin' }
  }
  goToLogin(){
    this.router.navigate(['login'])
  }
}
